"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import { Plus, ChevronDown, ChevronRight } from "lucide-react";
import { mockBuildings } from "../data/mockData";

export default function CompaniesDashboard() {
  const { buildingId, floorId } = useParams();

  const [floor, setFloor] = useState<any>(null);
  const [showAddCompany, setShowAddCompany] = useState(false);
  const [companyName, setCompanyName] = useState("");
  const [expandedInvoices, setExpandedInvoices] = useState<Set<string>>(new Set());

  useEffect(() => {
    const building = mockBuildings.find(b => b.id === buildingId);
    const f = building?.floors.find(fl => fl.id === floorId);
    if (f) setFloor({ ...f });
  }, [buildingId, floorId]);

  const toggleInvoice = (id: string) => {
    const s = new Set(expandedInvoices);
    s.has(id) ? s.delete(id) : s.add(id);
    setExpandedInvoices(s);
  };

  const addCompany = () => {
    if (!companyName.trim()) return;

    floor.companies.push({
      id: Date.now().toString(),
      name: companyName,
      poNumber: "",
      poValue: 0,
      invoices: []
    });

    setFloor({ ...floor });
    setCompanyName("");
    setShowAddCompany(false);
  };

  const addInvoice = (companyId: string, data: any) => {
    const company = floor.companies.find((c: any) => c.id === companyId);
    if (!company) return;

    company.invoices.push({
      id: Date.now().toString(),
      ...data,
      bills: []
    });

    setFloor({ ...floor });
  };

  const addBill = (companyId: string, invoiceId: string, data: any) => {
    const company = floor.companies.find((c: any) => c.id === companyId);
    const invoice = company?.invoices.find((i: any) => i.id === invoiceId);
    if (!invoice) return;

    invoice.bills.push({
      id: Date.now().toString(),
      ...data
    });

    setFloor({ ...floor });
  };

  if (!floor) return null;

  return (
    <div className="p-6 text-black bg-gray-50 min-h-screen">
      {floor.companies.map((c: any) => (
        <div key={c.id} className="bg-white border rounded p-5 mb-6">
          {/* COMPANY HEADER */}
          <div className="flex justify-between items-center mb-4">
            <div>
              <h2 className="text-xl font-semibold uppercase">{c.name}</h2>
              {c.invoices.length === 0 && (
                <p className="text-gray-500 text-sm">No invoices yet</p>
              )}
            </div>

            <InvoiceForm
              onAdd={(data) => addInvoice(c.id, data)}
              compact
            />
          </div>

          {/* INVOICES */}
          {c.invoices.map((inv: any) => {
            const open = expandedInvoices.has(inv.id);

            return (
              <div key={inv.id} className="border-t pt-3 mt-3">
                {/* INVOICE ROW */}
                <div
                  className="flex items-center gap-2 cursor-pointer"
                  onClick={() => toggleInvoice(inv.id)}
                >
                  {open ? <ChevronDown size={18} /> : <ChevronRight size={18} />}

                  <div className="flex-1">
                    <div className="font-medium">
                      {inv.name} — ₹{inv.value}
                    </div>
                    <div className="text-sm text-gray-500">{inv.date}</div>
                  </div>

                  {inv.fileUrl && (
                    <a
                      href={inv.fileUrl}
                      target="_blank"
                      className="text-blue-600 text-sm"
                      onClick={(e) => e.stopPropagation()}
                    >
                      View PDF
                    </a>
                  )}
                </div>

                {/* BILLS DROPDOWN */}
                {open && (
                  <div className="ml-6 mt-3">
                    <BillForm onAdd={(data) => addBill(c.id, inv.id, data)} />

                    {inv.bills.map((b: any) => (
                      <div key={b.id} className="mt-2 text-sm">
                        {b.name} — ₹{b.value} ({b.date})
                        {b.fileUrl && (
                          <a
                            href={b.fileUrl}
                            target="_blank"
                            className="text-blue-600 ml-2"
                          >
                            PDF
                          </a>
                        )}
                      </div>
                    ))}
                  </div>
                )}
              </div>
            );
          })}
        </div>
      ))}

      {/* ADD COMPANY */}
      <button
        onClick={() => setShowAddCompany(true)}
        className="mt-4 bg-blue-600 text-white px-4 py-2 rounded flex items-center gap-2"
      >
        <Plus size={16} /> Add Company
      </button>

      {showAddCompany && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center">
          <div className="bg-white p-6 rounded w-[400px]">
            <h2 className="font-semibold mb-4">Add Company</h2>
            <input
              className="w-full border p-2 mb-4"
              placeholder="Company Name"
              value={companyName}
              onChange={e => setCompanyName(e.target.value)}
            />
            <div className="flex justify-end gap-2">
              <button
                onClick={() => setShowAddCompany(false)}
                className="px-4 py-2 bg-gray-200"
              >
                Cancel
              </button>
              <button
                onClick={addCompany}
                className="px-4 py-2 bg-blue-600 text-white"
              >
                Add
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

/* ---------- FORMS ---------- */

function InvoiceForm({
  onAdd,
  compact
}: {
  onAdd: (d: any) => void;
  compact?: boolean;
}) {
  const [name, setName] = useState("");
  const [value, setValue] = useState("");
  const [date, setDate] = useState("");
  const [file, setFile] = useState<File | null>(null);

  return (
    <div className={`flex gap-2 ${compact ? "" : "mt-3"}`}>
      <input placeholder="Invoice Name" className="border p-2" onChange={e => setName(e.target.value)} />
      <input type="number" placeholder="Value" className="border p-2" onChange={e => setValue(e.target.value)} />
      <input type="date" className="border p-2" onChange={e => setDate(e.target.value)} />
      <input type="file" accept="application/pdf" onChange={e => setFile(e.target.files?.[0] || null)} />
      <button
        onClick={() =>
          onAdd({
            name,
            value: Number(value),
            date,
            fileUrl: file ? URL.createObjectURL(file) : undefined
          })
        }
        className="bg-blue-600 text-white px-3 rounded"
      >
        + Invoice
      </button>
    </div>
  );
}

function BillForm({ onAdd }: { onAdd: (d: any) => void }) {
  const [name, setName] = useState("");
  const [value, setValue] = useState("");
  const [date, setDate] = useState("");
  const [file, setFile] = useState<File | null>(null);

  return (
    <div className="flex gap-2 mt-2">
      <input placeholder="Bill Name" className="border p-2" onChange={e => setName(e.target.value)} />
      <input type="number" placeholder="Value" className="border p-2" onChange={e => setValue(e.target.value)} />
      <input type="date" className="border p-2" onChange={e => setDate(e.target.value)} />
      <input type="file" accept="application/pdf" onChange={e => setFile(e.target.files?.[0] || null)} />
      <button
        onClick={() =>
          onAdd({
            name,
            value: Number(value),
            date,
            fileUrl: file ? URL.createObjectURL(file) : undefined
          })
        }
        className="bg-green-600 text-white px-3 rounded"
      >
        + Bill
      </button>
    </div>
  );
}
